
body{
    flex-direction: column;
    color: white;
    background-color: black;
    margin: 0px;
}

.header-distributed{
    background-color: black;
    box-sizing: border-box;
    padding: 45px 50px;
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    h1{ 
        font-size: 60pt;
        padding: 0px;
        margin: 0px;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }
        .info-header{
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            text-decoration: none;
        }
    }


.section_body{
    display: flex;
    justify-content: center;

    .apollon{
        border-radius: 39pt;
        margin: 25px;
    }
}

.text{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    font-size: 20pt;
    padding: 60px;
}

.img_films{
    display: flex;
    justify-content: space-around;
    flex-direction: row;

.img_film{
    border-radius: 29px;
    
    }
}



.footer-distributed {
    margin-top: 100px;
    background-color: rgb(26, 24, 24);;
    box-sizing: border-box;
    width: 100%;
    text-align: left;
    font: normal 16px sans-serif;
    padding: 45px 50px;

  }
  
  .footer-distributed p {
    font-size: 14px;
   
  }
  
  .footer-distributed p.footer-links {
    font-size: 18px;
    font-weight: bold;                              
    color: white;
    margin: 0 0 10px;

  }
  
  .footer-distributed p.footer-links a {
   
    text-decoration: none;
    color: white;
  }
  
  
  .footer-distributed .footer-logo {
    float: right;
    margin-top: 6px;
    max-width: 180px;
  }
  
  .footer-distributed .footer-logo a {
    display: inline-block;
    width: 35px;
    height: 35px;
    background-color: #33383b;
    border-radius: 2px;
    font-size: 20px;
    color: white;
    text-align: center;
    line-height: 35px;
    margin-left: 3px;
    transition:all .25s;
}
  